.popup-login__input {
    font-family: Inter, Arial, sans-serif;
    font-size: 14px;
    line-height: 17px;
    max-height: 14px;
    border: none;
    display: block;
    background-color: black;
    color: white;
    margin: 0 auto 5px;
    padding: 0 0 13.26px;
    width: calc(100% - 36px * 2);
    border-bottom: 2px solid #CCCCCC;
}

.popup-login__input:last-of-type {
    margin-top: 30px;
    margin-bottom: 0;
}

@media (max-width: 600px) {
    .popup-login__input {
        width: calc(100% - 22px * 2);
        padding-bottom: 13px;
    }
}