.popup-login__title {
    font-family: Inter, Arial, sans-serif;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    padding: 0;
    color: white;
    margin: 60px auto 54px;
    max-width: 330px;
    text-align: center;
}

@media (max-width: 600px) {
    .popup-login__title {
        width: calc(100% - 22px * 2);
        margin: 25px auto 75px;
        font-size: 18px;
        line-height: 22px;
    }
}